<template>
  <div id="app">
    <div v-if="!isAuthenticated" class="row nav-panel mt-2 mb-2">
        <div class="col-6">
            <img class="logo" src="/1259-logo.png" alt="Team 1259 Logo">
        </div>
        <div class="col-6 align-text-bottom">
          <router-link :to="{ name: 'sign-in' }">Log In</router-link> |
          <router-link :to="{ name: 'sign-up' }">Sign Up</router-link>
        </div>
    </div>
    <router-view />
  </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            isAuthenticated: false,
            scouter: null,
            scouterTeam: null,
            authKey: null
        }
    },
    methods: {
      authenticated() {
        this.isAuthenticated = true;
      },
      signOut() {
        this.isAuthenticated = false
        axios
          .put(
            'https://frc.swentyl.com/API/Users/Users.php' +
            '?Update=logout' +
            '&Email=' + this.scouter +
            '&Auth_Key=' + this.authKey)
          .then(response => {
            console.log('Successful logoff: ' + response.data.extra_response)
            this.$router.push( { name: 'sign-in' } );
          })
          .catch(error => {
            console.log('Error calling logoff: ', error.response)
            this.$router.push( { name: 'sign-in' } );
          })
      },
      updateAuthKey( credentials ) {
          console.log("updateAuthKey")
          this.scouter = credentials.scouter;
          this.scouterTeam = credentials.scouterTeam;
          this.authKey = credentials.authKey
      }
    },
    created() {
      this.$eventHub.$on('authenticated', this.authenticated)
      this.$eventHub.$on('signOut', () => {
        this.signOut()
      })
      this.$eventHub.$on('authKeyPublished', (credentials) => {
          console.log("authKeyPublished payload = " + credentials);
          this.updateAuthKey(credentials);
      } )
    },
    beforeDestroy() {
      this.$eventHub.$off('authenticated');
    }
  }
</script>

