import Vue from "vue";
import Router from "vue-router";
import MatchAdd from "./views/MatchAdd.vue";
import SignIn from "./views/SignIn.vue";
import SignUp from "./views/SignUp.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: SignIn
    },
    {
      path: "/match/add",
      name: "match-add",
      component: MatchAdd
    },
    {
      path: "/signin",
      name: "sign-in",
      component: SignIn
    },
    {
      path: "/signup",
      name: "sign-up",
      component: SignUp
    }
  ]
});
