<template>
<div>
    <div class="alert alert-danger" role="alert" v-show="errorMessage">
      {{ errorMessage }}
    </div>
    <form class="review-form" @submit.prevent="onSubmit">
      <div class="row">
        <label for="email" class="col-5 col-form-label">Email</label>
        <div class="col-7">
          <input id="email" class="border float-right" type="email" v-model="email" required>
        </div>
      </div>
      <div class="row">
        <label for="password" class="col-5 col-form-label">Password</label>
        <div class="col-7">
          <input id="password" class="border float-right" v-model="password" type="password" required>
        </div>
      </div>
      <div class="form-actions">
        <button class="btn btn-primary" type="submit" :disabled="loading">
          <span :class="{ 'spinner-border spinner-border-sm': !loading }" role="status" aria-hidden="true"></span>
            Log In
        </button>
      </div>
    </form>
</div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            email: null,
            password: null,
            loading: false,
            errorMessage: null
        }
    },
    methods: {
        onSubmit() {
          this.loading = true
          this.errorMessage = null
          axios
            .put(
              'https://frc.swentyl.com/API/Users/Users.php' +
              '?Update=login' +
              '&Email=' + this.email +
              '&Password=' + this.password)
            .then(response => {
              console.log('Successful login: ' + response.data.extra_response)
              this.password = null;
              if ( response.data.extra_response ) {
                this.$eventHub.$emit('authenticated');
                const credentials = {
                  scouter: this.email,
                  scouterTeam: "1259",
                  authKey: response.data.extra_response
                }
                this.$eventHub.$emit('authKeyPublished', credentials)
                this.loading = false
                this.$router.push( { name: 'match-add' } );
              }
              else {
                this.errorMessage = "Failed to login"
                this.loading = false
              }
            })
            .catch(error => {
              console.log('Error calling login: ', error.response)
              this.errorMessage = "Failed to login"
              this.password = null
              this.loading = false
            })
        }
    }
}
</script>