<template>
    <div>
        <div class="row nav-panel mt-2 mb-2">
            <div class="col-6">
                <img class="logo" src="/1259-logo.png" alt="Team 1259 Logo">
            </div>
            <div v-if="checkedIn" class="col-6 align-text-bottom">
                <a href="#" @click="checkOut">Check Out ({{ this.alliance }})</a>
            </div>
            <div v-else class="col-6 align-text-bottom">
                <a href="#" @click="signOut">Log Out</a>
            </div>
        </div>
        <div id="successMessage" class="alert alert-success" role="alert" style="display:none;">
          {{ successMessage }}
        </div>
        <div class="alert alert-danger" role="alert" v-show="errorMessage">
          {{ errorMessage }}
        </div>
        <div v-if="checkedIn">
        <form class="review-form" @submit.prevent="onSubmit">
            <div class="row">
                <label for="matchNumber" class="col-5">Match Number</label>
                <div class="col-7">
                    <input class="border float-right" id="matchNumber" v-model="matchNumber"/>
                </div>
            </div>
            <div class="row">
                <label for="teamNumber" class="col-5">Team Number</label>
                <div class="col-7">
                    <input class="border float-right" id="teamNumber" v-model="teamNumber"/>
                </div>
            </div>
            <div class="row">
                <label for="dead" class="col-10">Robot was dead or unable to move?</label>
                <div class="col-2">
                    <input type="checkbox" id="dead" v-model="dead">
                </div>
            </div>
            <h2>Sandstorm</h2>
            <div class="row">
                <label for="secondLevel" class="col-10">Started on Second Level?</label>
                <div class="col-2">
                    <input type="checkbox" id="secondLevel" v-model="secondLevel">
                </div>
            </div>
            <div class="row">
                <label for="crossedLine" class="col-10">Crossed Line?</label>
                <div class="col-2">
                    <input type="checkbox" id="crossedLine" v-model="crossedLine">
                </div>
            </div>
            <div class="row">
                <label for="sandLowHatchSuccess" class="col-8">Low Hatch Success</label>
                <div class="col-4">
                    <vue-numeric-input id="sandLowHatchSuccess" class="float-right" v-model="sandLowHatchSuccess" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <div class="row">
                <label for="sandHighHatchSuccess" class="col-8">High Hatch Success</label>
                <div class="col-4">
                    <vue-numeric-input id="sandHighHatchSuccess" class="float-right" v-model="sandHighHatchSuccess" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <div class="row">
                <label for="sandLowCargoSuccess" class="col-8">Low Cargo Success</label>
                <div class="col-4">
                    <vue-numeric-input id="sandLowCargoSuccess" class="float-right" v-model="sandLowCargoSuccess" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <div class="row">
                <label for="sandHighCargoSuccess" class="col-8">High Cargo Success</label>
                <div class="col-4">
                    <vue-numeric-input id="sandHighCargoSuccess" class="float-right" v-model="sandHighCargoSuccess" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <h2>Teleop</h2>
            <div class="row">
                <label for="teleLowHatchesFail" class="col-8">Low Hatch Fail</label>
                <div class="col-4">
                    <vue-numeric-input id="teleLowHatchesFail" class="float-right" v-model="teleLowHatchesFail" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <div class="row">
                <label for="teleLowHatchesSuccess" class="col-8">Low Hatch Success</label>
                <div class="col-4">
                    <vue-numeric-input id="teleLowHatchesSuccess" class="float-right" v-model="teleLowHatchesSuccess" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <hr>
            <div class="row">
                <label for="teleHighHatchesFail" class="col-8">High Hatch Fail</label>
                <div class="col-4">
                    <vue-numeric-input id="teleHighHatchesFail" class="float-right" v-model="teleHighHatchesFail" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <div class="row">
                <label for="teleHighHatchesSuccess" class="col-8">High Hatch Success</label>
                <div class="col-4">
                    <vue-numeric-input id="teleHighHatchesSuccess" class="float-right" v-model="teleHighHatchesSuccess" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <hr>
            <div class="row">
                <label for="teleLowCargoFail" class="col-8">Low Cargo Fail</label>
                <div class="col-4">
                    <vue-numeric-input id="teleLowCargoFail" class="float-right" v-model="teleLowCargoFail" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <div class="row">
                <label for="teleLowCargoSuccess" class="col-8">Low Cargo Success</label>
                <div class="col-4">
                    <vue-numeric-input id="teleLowCargoSuccess" class="float-right" v-model="teleLowCargoSuccess" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <hr>
            <div class="row">
                <label for="teleHighCargoFail" class="col-8">High Cargo Fail</label>
                <div class="col-4">
                    <vue-numeric-input id="teleHighCargoFail" class="float-right" v-model="teleHighCargoFail" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <div class="row">
                <label for="teleHighCargoSuccess" class="col-8">High Cargo Success</label>
                <div class="col-4">
                    <vue-numeric-input id="teleHighCargoSuccess" class="float-right" v-model="teleHighCargoSuccess" style="width: 90px;" :min="0" :max="1000" :step="1"/>
                </div>
            </div>
            <hr>
            <div class="row">
                <label for="placedLevel3Piece" class="col-10">Placed at least one level 3 piece?</label>
                <div class="col-2">
                    <input type="checkbox" id="placedLevel3Piece" v-model="placedLevel3Piece">
                </div>
            </div>
            <h2>End Game</h2>
            <div class="row">
                <label for="level1Climb" class="col-10">Level 1 Successful Climb</label>
                <div class="col-2">
                    <input type="checkbox" id="level1Climb" v-model="level1Climb">
                </div>
            </div>
            <div class="row">
                <label for="level2Climb" class="col-10">Level 2 Successful Climb</label>
                <div class="col-2">
                    <input type="checkbox" id="level2Climb" v-model="level2Climb">
                </div>
            </div>
            <div class="row">
                <label for="level3Climb" class="col-10">Level 3 Successful Climb</label>
                <div class="col-2">
                    <input type="checkbox" id="level3Climb" v-model="level3Climb">
                </div>
            </div>
            <div class="form-actions">
                <button class="btn btn-primary" type="submit" :disabled="loading">
                    <span :class="{ 'spinner-border spinner-border-sm': !loading }" role="status" aria-hidden="true"></span>
                        Submit
                </button>
            </div>
        </form>
        </div>
        <div v-else>
        <form class="review-form" @submit.prevent="onCheckIn">
            <div class="row">
                <label for="alliance" class="col-5">Alliance</label>
                <div class="col-7">
                    <select class="border float-right" id="alliance" v-model="alliance" style="width: 150px;">
                        <option value="R1">R1</option>
                        <option value="R2">R2</option>
                        <option value="R3">R3</option>
                        <option value="B1">B1</option>
                        <option value="B2">B2</option>
                        <option value="B3">B3</option>
                    </select>
                </div>
            </div>
            <div class="form-actions">
                <button class="btn btn-primary" type="submit" :disabled="loading">
                    <span :class="{ 'spinner-border spinner-border-sm': !loading }" role="status" aria-hidden="true"></span>
                        Check In
                </button>
            </div>
        </form>
        </div>
    </div>
</template>

<script>

import axios from 'axios'
import VueNumericInput from 'vue-numeric-input';

export default {
    data() {
        return {
            checkedIn: false,
            loading: false,
            successMessage: null,
            errorMessage: null,
            scouter: null,
            scouterTeam: null,
            authKey: null,
            displayName: 'UNKNOWN',
            teamNumber: null,
            matchNumber: null,
            alliance: null,
            set: 'S1',
            dead: false,
            secondLevel: false,
            crossedLine: false,
            sandHighHatchSuccess: 0,
            sandLowHatchSuccess: 0,
            sandHighCargoSuccess: 0,
            sandLowCargoSuccess: 0,
            teleHighHatchesSuccess: 0,
            teleHighHatchesFail: 0,
            teleLowHatchesSuccess: 0,
            teleLowHatchesFail: 0,
            teleHighCargoSuccess: 0,
            teleHighCargoFail: 0,
            teleLowCargoSuccess: 0,
            teleLowCargoFail: 0,
            placedLevel3Piece: false,
            level1Climb: false,
            level2Climb: false,
            level3Climb: false
        }
    },
    computed: {
        // a computed getter
        allianceSet: function () {
            return this.alliance + "_" + this.set
        }
    },
    methods: {
        onSubmit() {
            this.loading = true
            this.errorMessage = null
            // axios call here
            console.log('Submit data now...')

            let uri = 
                'https://frc.swentyl.com/API/ScouterDefaultWorkspace/ScouterDefault.php' +
                '?Update=submitData' +
                '&Scouter=' + this.scouter +
                '&Auth_Key=' + this.authKey +
                '&Team=' + this.scouterTeam +
                '&Data=' + this.displayName +
                "," + this.scouterTeam +
                "," + this.allianceSet +
                ";" + this.teamNumber +
                "," + this.matchNumber +
                "," + this.alliance +
                "," + (this.dead ? 'Y' : 'N') +
                "," + (this.secondLevel ? 'Y' : 'N') +
                "," + (this.crossedLine ? 'Y' : 'N') +
                "," + this.sandHighHatchSuccess +
                "," + this.sandLowHatchSuccess +
                "," + this.sandHighCargoSuccess +
                "," + this.sandLowCargoSuccess +
                "," + this.teleHighHatchesSuccess +
                "," + this.teleHighHatchesFail +
                "," + this.teleLowHatchesSuccess +
                "," + this.teleLowHatchesFail +
                "," + this.teleHighCargoSuccess +
                "," + this.teleHighCargoFail +
                "," + this.teleLowCargoSuccess +
                "," + this.teleLowCargoFail +
                "," + (this.placedLevel3Piece ? 'Y' : 'N') +
                "," + (this.level1Climb ? 'Y' : 'N') +
                "," + (this.level2Climb ? 'Y' : 'N') +
                "," + (this.level3Climb ? 'Y' : 'N');
            console.log('About to put: ' + uri)
            let currentMatch = this.matchNumber
            axios
            .put(
                uri
            )
            .then(response => {
                this.successMessage = "Submitted Match " + currentMatch
                document.getElementById("successMessage").style.display = "block";
                setTimeout( function() {
                    document.getElementById("successMessage").style.display = "none";
                }, 2000 );
                this.loading = false
            })
            .catch(error => {
                console.log('Match data post: Error = ', error.response)
                this.errorMessage = "Failed to submit data. Error = " + error.response
                this.loading = false
            })

            this.clearFields();
            window.scrollTo(0,0);
        },
        clearFields() {
            this.matchNumber = ''
            this.teamNumber = ''
            this.dead = false
            this.secondLevel = false
            this.crossedLine = false
            this.sandLowHatchSuccess = 0
            this.sandHighHatchSuccess = 0
            this.sandLowCargoSuccess = 0
            this.sandHighCargoSuccess = 0
            this.teleLowHatchesFail = 0
            this.teleLowHatchesSuccess = 0
            this.teleHighHatchesFail = 0
            this.teleHighHatchesSuccess = 0
            this.teleLowCargoFail = 0
            this.teleLowCargoSuccess = 0
            this.teleHighCargoFail = 0
            this.teleHighCargoSuccess = 0
            this.placedLevel3Piece = false
            this.level1Climb = false
            this.level2Climb = false
            this.level3Climb = false
        },
        onCheckIn() {
            this.loading = true
            this.errorMessage = null
            console.log('Check In now...')

            if ( !this.alliance ) {
                this.errorMessage = "Please select your Alliance"
                this.loading = false
                return;
            }
            let uri = 
                'https://frc.swentyl.com/API/ScouterDefaultWorkspace/ScouterDefault.php' +
                '?Update=checkIn' +
                '&Scouter=' + this.scouter +
                '&Auth_Key=' + this.authKey +
                '&Team=' + this.scouterTeam +
                '&Alliance=' + this.alliance
            console.log('About to put: ' + uri)
            axios
            .put(
                uri
            )
            .then(response => {
                console.log('Check In: Success')
                this.checkedIn = true
                this.loading = false
            })
            .catch(error => {
                console.log('Check In: Error = ', error.response)
                this.errorMessage = "Failed to check in. Error = error.response"
                this.loading = false
            })
        },
        checkOut() {
            this.loading = true
            this.clearFields()
            this.errorMessage = null
            console.log('Check Out...')
            let uri = 
                'https://frc.swentyl.com/API/ScouterDefaultWorkspace/ScouterDefault.php' +
                '?Update=checkOut' +
                '&Scouter=' + this.scouter +
                '&Auth_Key=' + this.authKey +
                '&Team=' + this.scouterTeam +
                '&Alliance=' + this.alliance
            axios
            .put(
                uri
            )
            .then(response => {
                console.log('Check Out: Success')
                this.checkedIn = false
                this.loading = false
            })
            .catch(error => {
                console.log('Check Out: Error = ', error.response)
                this.errorMessage = "Failed to check out. Error = error.response"
                this.loading = false
            })
        },
        signOut() {
            this.$eventHub.$emit('signOut');
        },
        updateAuthKey( credentials ) {
            console.log("updateAuthKey")
            this.scouter = credentials.scouter;
            this.scouterTeam = credentials.scouterTeam;
            this.authKey = credentials.authKey
        }
    },
    components: {
        VueNumericInput
    },
    mounted() {
        this.$eventHub.$on('authKeyPublished', (credentials) => {
            console.log("authKeyPublished payload = " + credentials);
            this.updateAuthKey(credentials);
        } )
    },
    updated() {
        var numericInputs = document.getElementsByClassName("numeric-input"); 
        for (var i = 0; i < numericInputs.length; i++) { 
            numericInputs[i].readOnly = true;
        }
    }
}
</script>
