<template>
    <div>
        <div class="alert alert-danger" role="alert" v-show="errorMessage">
            {{ errorMessage }}
        </div>
        <form class="review-form" @submit.prevent="onSubmit">
            <div class="row">
                <label for="firstName" class="col-5 col-form-label">First Name</label>
                <div class="col-7">
                <input id="firstName" class="border float-right" v-model="firstName" required>
                </div>
            </div>
            <div class="row">
                <label for="lastName" class="col-5 col-form-label">Last Name</label>
                <div class="col-7">
                <input id="lastName" class="border float-right" v-model="lastName" required>
                </div>
            </div>
            <div class="row">
                <label for="teamNumber" class="col-5 col-form-label">Team Number</label>
                <div class="col-7">
                <input id="teamNumber" class="border float-right" v-model="teamNumber" required>
                </div>
            </div>
            <div class="row">
                <label for="email" class="col-5 col-form-label">Email</label>
                <div class="col-7">
                <input id="email" class="border float-right" type="email" v-model="email" required>
                </div>
            </div>
            <div class="row">
                <label for="password" class="col-5 col-form-label">Password</label>
                <div class="col-7">
                <input id="password" class="border float-right" v-model="password" type="password" required>
                </div>
            </div>
            <div class="row">
                <label for="confirmPassword" class="col-5 col-form-label">Confirm Password</label>
                <div class="col-7">
                <input id="confirmPassword" class="border float-right" v-model="confirmPassword" type="password" required>
                </div>
            </div>
            <div class="row">
                <label for="accessCode" class="col-5 col-form-label">Access Code</label>
                <div class="col-7">
                <input id="accessCode" class="border float-right" v-model="accessCode" required>
                </div>
            </div>
            <div class="form-actions">
                <button class="btn btn-primary" type="submit" :disabled="loading">
                    <span :class="{ 'spinner-border spinner-border-sm': !loading }" role="status" aria-hidden="true"></span>
                        Sign Up
                </button>
            </div>
        </form>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            firstName: null,
            lastName: null,
            teamNumber: "1259",
            email: null,
            password: null,
            confirmPassword: null,
            authKey: null,
            accessCode: '1259_8o5l7i',
            loading: false,
            errorMessage: null
        }
    },
    methods: {
        onSubmit() {
            this.loading = true
            this.errorMessage = null
            if ( this.password !== this.confirmPassword ) {
                this.errorMessage = "Password and Confirm Password must match"
                this.loading = false
                return
            }
            axios
              .post('https://frc.swentyl.com/API/RegisterUsers/registerUser.php' + 
              '?First_Name=' + this.firstName +
              '&Last_Name=' + this.lastName +
              '&Email=' + this.email +
              '&Password=' + this.password +
              '&Team=1259&Alliance=R_1')
              .then(response => {
                this.password = null;
                if ( response.data.extra_response ) {
                    this.authKey = response.data.extra_response
                    axios
                        .put('https://frc.swentyl.com/API/Scouting/Scouting.php' +
                        '?Update=joinTeamWithCode' +
                        '&Member=' + this.email +
                        '&Auth_Key=' + this.authKey +
                        '&Access_Code=' + this.accessCode)
                        .then(response => {
                            this.$router.push( { name: 'sign-in' } );
                        })
                        .catch(error => {
                            this.errorMessage = "Failed to join team"
                            this.loading = false
                            this.password = null;
                            console.log('Error calling joinTeamWithCode: ', error.response)
                        })
                }
                else {
                    this.errorMessage = "Failed to sign up"
                }
                this.loading = false
              })
              .catch(error => {
                this.errorMessage = "Failed to sign up"
                this.loading = false
                this.password = null;
                console.log('Error calling registerUser: ', error.response)
              })
        }
    }
}
</script>